<template>
  <div class="user-products-container">
    <el-card class="products-card">
      <template #header>
        <div class="card-header">
          <span>我的商品</span>
          <el-button type="primary" @click="goToCreate" class="create-btn">
            <el-icon><Plus /></el-icon>
            发布新商品
          </el-button>
        </div>
      </template>
      
      <div class="search-filter">
        <el-input
          v-model="searchQuery"
          placeholder="搜索商品名称"
          prefix-icon="el-icon-search"
          class="search-input"
          clearable
        />
        <el-select v-model="productStatus" placeholder="选择状态" clearable class="status-select">
          <el-option label="全部" value="" />
          <el-option label="上架" value="1" />
          <el-option label="下架" value="0" />
        </el-select>
        <el-select v-model="productCategory" placeholder="选择分类" clearable class="category-select">
          <el-option label="全部" value="" />
          <el-option label="电子产品" value="1" />
          <el-option label="图书文具" value="2" />
          <el-option label="服装鞋帽" value="3" />
          <el-option label="生活用品" value="4" />
          <el-option label="其他" value="5" />
        </el-select>
      </div>
      
      <el-table
        v-loading="loading"
        :data="filteredProducts"
        style="width: 100%"
        :empty-text="emptyText"
      >
        <el-table-column prop="id" label="商品ID" width="80" />
        <el-table-column prop="name" label="商品名称" min-width="200">
          <template #default="scope">
            <div class="product-name-wrapper">
              <img :src="getFirstImage(scope.row.images)" class="product-thumbnail" />
              <span class="product-name">{{ scope.row.name }}</span>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="categoryName" label="分类" width="100" />
        <el-table-column prop="price" label="价格" width="100">
          <template #default="scope">¥{{ scope.row.price }}</template>
        </el-table-column>
        <el-table-column prop="status" label="状态" width="80">
          <template #default="scope">
            <el-tag :type="scope.row.status === 1 ? 'success' : 'info'">
              {{ scope.row.status === 1 ? '上架' : '下架' }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="viewCount" label="浏览量" width="80" />
        <el-table-column prop="createdTime" label="创建时间" width="180" />
        <el-table-column prop="updateTime" label="更新时间" width="180" />
        <el-table-column label="操作" width="200" fixed="right">
          <template #default="scope">
            <el-button type="primary" size="small" @click="viewProduct(scope.row.id)" class="btn-view">
              查看
            </el-button>
            <el-button type="success" size="small" @click="toggleStatus(scope.row)" v-if="scope.row.status === 1" class="btn-status">
              下架
            </el-button>
            <el-button type="success" size="small" @click="toggleStatus(scope.row)" v-else class="btn-status">
              上架
            </el-button>
            <el-button type="warning" size="small" @click="editProduct(scope.row.id)" class="btn-edit">
              编辑
            </el-button>
            <el-button type="danger" size="small" @click="deleteProduct(scope.row)" class="btn-delete">
              删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      
      <div class="pagination-container">
        <el-pagination
          v-model:current-page="currentPage"
          v-model:page-size="pageSize"
          :page-sizes="[10, 20, 50, 100]"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </el-card>
    
    <!-- 确认对话框 -->
    <el-dialog v-model="confirmDialogVisible" :title="confirmDialogTitle" width="30%">
      <div>{{ confirmDialogContent }}</div>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="confirmDialogVisible = false">取消</el-button>
          <el-button type="danger" @click="confirmAction">确认</el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { Plus } from '@element-plus/icons-vue'
import { productAPI } from '../api'

export default {
  name: 'UserProducts',
  components: {
    Plus
  },
  data() {
    return {
      products: [],
      loading: false,
      searchQuery: '',
      productStatus: '',
      productCategory: '',
      currentPage: 1,
      pageSize: 10,
      total: 0,
      confirmDialogVisible: false,
      confirmDialogTitle: '',
      confirmDialogContent: '',
      actionProduct: null,
      actionType: ''
    }
  },
  computed: {
    filteredProducts() {
      let filtered = this.products
      
      // 搜索过滤
      if (this.searchQuery) {
        filtered = filtered.filter(product => 
          product.name.toLowerCase().includes(this.searchQuery.toLowerCase())
        )
      }
      
      // 状态过滤
      if (this.productStatus !== '') {
        filtered = filtered.filter(product => product.status.toString() === this.productStatus)
      }
      
      // 分类过滤
      if (this.productCategory !== '') {
        filtered = filtered.filter(product => product.categoryId.toString() === this.productCategory)
      }
      
      // 分页
      this.total = filtered.length
      const startIndex = (this.currentPage - 1) * this.pageSize
      const endIndex = startIndex + this.pageSize
      return filtered.slice(startIndex, endIndex)
    },
    emptyText() {
      if (this.loading) return '加载中...'
      return '暂无商品'
    }
  },
  mounted() {
    this.loadUserProducts()
  },
  watch: {
    // 监听筛选条件变化，重置页码
    searchQuery() {
      this.currentPage = 1
    },
    productStatus() {
      this.currentPage = 1
    },
    productCategory() {
      this.currentPage = 1
    }
  },
  methods: {
    async loadUserProducts() {
      this.loading = true
      try {
        const response = await productAPI.getUserProducts()
        // 格式化数据
        this.products = response.map(product => ({
          ...product,
          categoryName: this.getCategoryName(product.categoryId),
          createdTime: this.formatDate(product.createdTime),
          updateTime: this.formatDate(product.updateTime)
        }))
      } catch (error) {
        this.$message.error('加载商品列表失败')
        this.products = []
      } finally {
        this.loading = false
      }
    },
    getCategoryName(categoryId) {
      const categoryMap = {
        1: '电子产品',
        2: '图书文具',
        3: '服装鞋帽',
        4: '生活用品',
        5: '其他'
      }
      return categoryMap[categoryId] || '未知'
    },
    getFirstImage(images) {
      if (!images) return 'https://img.alicdn.com/imgextra/i4/O1CN01q0UcHt1h1sVt0jA8O_!!6000000004122-2-tps-112-112.png'
      const imageArray = typeof images === 'string' ? JSON.parse(images) : images
      return imageArray && imageArray.length > 0 ? imageArray[0] : 'https://img.alicdn.com/imgextra/i4/O1CN01q0UcHt1h1sVt0jA8O_!!6000000004122-2-tps-112-112.png'
    },
    formatDate(dateString) {
      if (!dateString) return ''
      const date = new Date(dateString)
      return date.toLocaleString('zh-CN')
    },
    goToCreate() {
      this.$router.push('/product/create')
    },
    viewProduct(productId) {
      this.$router.push(`/product/detail/${productId}`)
    },
    editProduct(productId) {
      this.$router.push(`/product/update/${productId}`)
    },
    toggleStatus(product) {
      this.actionProduct = product
      this.actionType = 'toggle'
      this.confirmDialogTitle = product.status === 1 ? '确认下架商品' : '确认上架商品'
      this.confirmDialogContent = product.status === 1 ? '下架后，商品将不再在首页显示，但可以随时重新上架。确定要下架该商品吗？' : '上架后，商品将在首页显示。确定要上架该商品吗？'
      this.confirmDialogVisible = true
    },
    deleteProduct(product) {
      this.actionProduct = product
      this.actionType = 'delete'
      this.confirmDialogTitle = '确认删除商品'
      this.confirmDialogContent = '删除后将无法恢复，确定要删除该商品吗？'
      this.confirmDialogVisible = true
    },
    async confirmAction() {
      this.confirmDialogVisible = false
      
      if (this.actionType === 'toggle') {
        await this.performToggleStatus()
      } else if (this.actionType === 'delete') {
        await this.performDelete()
      }
    },
    async performToggleStatus() {
      const newStatus = this.actionProduct.status === 1 ? 0 : 1
      try {
        await productAPI.updateProductStatus(this.actionProduct.id, newStatus)
        // 更新本地数据
        const index = this.products.findIndex(p => p.id === this.actionProduct.id)
        if (index !== -1) {
          this.products[index].status = newStatus
        }
        this.$message.success(newStatus === 1 ? '商品上架成功' : '商品下架成功')
      } catch (error) {
        this.$message.error('操作失败，请重试')
      }
    },
    async performDelete() {
      try {
        await productAPI.deleteProduct(this.actionProduct.id)
        // 从本地数据中移除
        this.products = this.products.filter(p => p.id !== this.actionProduct.id)
        this.$message.success('商品删除成功')
      } catch (error) {
        this.$message.error('删除失败，请重试')
      }
    },
    handleSizeChange(size) {
      this.pageSize = size
    },
    handleCurrentChange(current) {
      this.currentPage = current
    }
  }
}
</script>

